    ScrollTo() {
        $(document).on('click', '*[data-scroll-to]', function(){
            var $this = $(this),
                data = $this.data();

            if(!$(data.scrollTo).length)
                return;

            var top = $(data.scrollTo).offset().top;
            if(data.scrollMargin)
                top -= data.scrollMargin;

            window.Konesso.behaviors.Scroll.Smooth(top);
        })
    }

