      description:
        '<p><span style="font-size: 10pt;"> Dobra kawa i fili\u017canki do kawy to nie wszystko. Do przygotowania Twojego ulubionego napoju potrzebny b\u0119dzie jeszcze ekspres do kawy lub inny rodzaj zaparzacza. W ofercie naszego sklepu znajdziesz szeroki wyb\u00f3r ekspres\u00f3w do kawy do u\u017cytku domowego, biurowego oraz profesjonalne ekspresy do kawy do kawiarni i restauracji. Ekspresy do kawy dost\u0119pne w ofercie naszego sklepu zosta\u0142y podzielone na kategorie wzgl\u0119dem producenta, rodzaju i swojego przeznaczenia. Oferujemy ekspresy znanych i cenionych na ca\u0142ym \u015bwiecie firm jak Jura, Nivona, Saeco, DeLonghi, Nuova Simonelli czy Lelit.</span></p>',
