      name: "Kolbowe ekspresy do kawy",
      description:
        "<p>Dobra kawa ziarnista jest podstaw\u0105 do przygotowania aromatycznej fili\u017canki kawy niczym w kawiarni, ale potrzebny jest jeszcze odpowiedni sprz\u0119t. Kolbowy ekspres do kawy to urz\u0105dzenie dzi\u0119ki, kt\u00f3remu w domu stworzysz w\u0142asn\u0105 kawiarnie. Parzenie kawy w ekspresie kolbowym na pierwszy rzut oka wygl\u0105da banalnie, jednak musisz wiedzie\u0107 \u017ce to prawdziwa sztuka i brak wiedzy oraz umiej\u0119tno\u015bci mo\u017ce skutkowa\u0107 niepowodzeniem. W ofercie naszego sklepu znajdziesz wysokiej jako\u015bci ekspresy kolbowe wielu znanych producent\u00f3w. Oferujemy ekspresy do domu oraz ekspresy do profesjonalnych zastosowa\u0144 np. restauracji, kawiarni czy hoteli.\u00a0</p>",
