      name: "Kawa mielona",
      description:
        "<p>Nie ka\u017cdy mi\u0142o\u015bnik kawy posiada w swoim domu m\u0142ynek, dzi\u0119ki kt\u00f3remu m\u00f3g\u0142by korzysta\u0107 z kawy ziarnistej. W\u0142a\u015bnie dlatego producenci kawy wymy\u015blili kawy mielone, kt\u00f3rymi mo\u017cesz si\u0119 delektowa\u0107 nawet je\u015bli nie posiadasz m\u0142ynka do kawy oraz ekspresu. W naszej ofercie znajdziesz najlepsze kawy mielone wielu znanych na ca\u0142ym \u015bwiecie producent\u00f3w. W\u015br\u00f3d kaw mielonych du\u017c\u0105 cze\u015b\u0107 stanowi\u0105 w\u0142oskie kawy, poniewa\u017c to W\u0142osi jako pierwsi wymy\u015blili i wprowadzili ten spos\u00f3b pakowania i eksportowania kawy do innych kraj\u00f3w.\u00a0</p>",
